.modal {
  height: 100vh;
  width: 100vw;
  background: rgba(0,0,0,0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(0);
}

.modal.active {
  transform: scale(1);
}
.modal__content {
   padding: 20px;
   border-radius: 12px;
   background-color: white;
  transform: scale(0);
  transition: 1.5s;
 }

.modal__content.active {
  padding: 20px;
  border-radius: 12px;
  background-color: white;
  transform: scale(1);
  transition: 1.5s;
}

.form-box {
  max-width: 300px;
  background: #f1f7fe;
  overflow: hidden;
  border-radius: 16px;
  color: #010101;
  display: none;
}

